import {
  CategoryScale,
  Chart as ChartJS,
  Filler,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  Title,
  Tooltip,
} from "chart.js";
import zoomPlugin from "chartjs-plugin-zoom";
import { ChartBarSquareIcon } from "@heroicons/react/24/solid";
import React, { useRef } from "react";
import { Line } from "react-chartjs-2";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  zoomPlugin,
  Title,
  Tooltip,
  Filler,
  Legend
);

const IntensityCard = ({ intensity }) => {
  const chartRef = useRef(null);

  const options = {
    scales: {
      x: {
        min: 0,
        max: 20,
        // ticks: { display: false },
        grid: {
          display: false,
          drawBorder: false,
        },
      },
      y: {
        beginAtZero: true,
        // ticks: { display: false },
        grid: {
          display: false,
          drawBorder: false,
        },
      },
    },
    responsive: true,
    plugins: {
      legend: {
        display: false,
      },
      title: {
        display: false,
        text: "Intensity",
      },
      zoom: {
        pan: {
          enabled: true,
          mode: "x",
        },
        zoom: {
          pinch: {
            enabled: true,
          },
          wheel: {
            enabled: true,
            speed: 0.05,
          },
          mode: "x",
        },
      },
    },
  };

  const labels = Array(1001)
    .fill(0)
    .map((_, i) => i);

  const data = {
    labels,
    datasets: [
      {
        fill: true,
        label: "Intensity",
        data: intensity,
        borderColor: "#65E765",
        backgroundColor: "#90ee90",
        pointHoverBackgroundColor: "#ff0000",
      },
    ],
  };

  return (
    <div className="p-4 border-2 rounded-xl border-slate-200">
      <div className="flex flex-col">
        <h2 className="text-2xl font-bold flex flex-row gap-2 items-center text-gray-600 text-left mt-8 mb-2 ms-6">
          <ChartBarSquareIcon className="h-8 w-8 text-gray-500" />
          Intensity
        </h2>
        <h4 className="text-left text-gray-400 ms-6 mb-8">
          Intensity of each topic
        </h4>
      </div>
      <Line ref={chartRef} height="100" options={options} data={data} />
    </div>
  );
};

export default IntensityCard;
